@tailwind base;
@tailwind components;
@tailwind utilities;

/* Mobile and Tablet Responsive Fixes */
* {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow-x: hidden; /* Prevent horizontal scroll */
  -webkit-overflow-scrolling: touch; /* Smooth scrolling on iOS */
  -webkit-tap-highlight-color: transparent; /* Remove tap highlight on mobile */
}

body {
  position: relative;
  min-height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

/* Prevent horizontal overflow */
#root {
  width: 100%;
  min-height: 100vh;
  overflow-x: hidden;
}

/* Container fixes for mobile */
.container {
  width: 100%;
  max-width: 100%;
  padding-left: 1rem;
  padding-right: 1rem;
}

@media (min-width: 640px) {
  .container {
    max-width: 640px;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

@media (min-width: 1024px) {
  .container {
    max-width: 1024px;
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

@media (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

@media (min-width: 1536px) {
  .container {
    max-width: 1536px;
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

/* Custom animations */
@keyframes blob {
  0% {
    transform: translate(0px, 0px) scale(1);
  }
  33% {
    transform: translate(30px, -50px) scale(1.1);
  }
  66% {
    transform: translate(-20px, 20px) scale(0.9);
  }
  100% {
    transform: translate(0px, 0px) scale(1);
  }
}

.animate-blob {
  animation: blob 7s infinite;
}

.animation-delay-2000 {
  animation-delay: 2s;
}

.animation-delay-4000 {
  animation-delay: 4s;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #3b82f6, #8b5cf6);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(to bottom, #2563eb, #7c3aed);
}

/* Smooth scrolling */
html {
  scroll-behavior: smooth;
}

/* Line clamp utility */
.line-clamp-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

/* Focus styles */
*:focus {
  outline: none;
}

/* Custom selection */
::selection {
  background: linear-gradient(to right, #3b82f6, #8b5cf6);
  color: white;
}

/* Loading animation */
@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.animate-spin {
  animation: spin 1s linear infinite;
}

/* Pulse animation */
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

.animate-pulse {
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

/* Gradient text */
.bg-gradient-text {
  background: linear-gradient(to right, #3b82f6, #8b5cf6);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Glass effect */
.glass {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

/* Hover effects */
.hover-lift {
  transition: transform 0.2s ease-in-out;
}

.hover-lift:hover {
  transform: translateY(-4px);
}

/* Custom button styles */
.btn-primary {
  @apply px-6 py-3 bg-gradient-to-r from-blue-500 to-purple-600 text-white rounded-lg font-medium hover:shadow-lg transition-all duration-200;
}

.btn-secondary {
  @apply px-6 py-3 border-2 border-gray-300 text-gray-700 rounded-lg font-medium hover:border-blue-500 hover:text-blue-500 transition-all duration-200;
}

/* Card styles */
.card {
  @apply bg-white rounded-xl shadow-lg hover:shadow-xl transition-all duration-300;
}

/* Section spacing */
.section-padding {
  @apply py-20;
}

/* Container max width */
.container {
  @apply max-w-7xl mx-auto;
}

/* Responsive text */
.text-responsive {
  @apply text-2xl md:text-3xl lg:text-4xl;
}

/* Custom shadows */
.shadow-custom {
  box-shadow: 0 10px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.shadow-custom-lg {
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

/* Mobile-specific fixes */
@media (max-width: 768px) {
  /* CLEAN MOBILE LAYOUT - LIKE DESKTOP */
  
  /* Basic mobile setup */
  * {
    box-sizing: border-box;
  }

  html, body {
    width: 100%;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
  }

  #root {
    width: 100%;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
  }

  /* Container and section spacing - FIX ALIGNMENT */
  .container {
    width: 100vw;
    max-width: 100vw;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    margin: 0;
  }

  section {
    width: 100vw;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    margin: 0;
  }

  /* Header fixes - FIX ALIGNMENT */
  header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100vw;
    z-index: 1000;
    margin: 0;
  }

  /* Hero section - FIX OVERLAP AND ALIGNMENT */
  #home {
    padding-top: 140px;
    padding-bottom: 3rem;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    margin: 0;
  }

  #home .relative {
    width: 100%;
    max-width: 100%;
    padding: 3rem 1.5rem;
    text-align: center;
    margin: 0 auto;
  }

  /* Text sizing for mobile - FIX CLIPPING */
  #home h1 {
    font-size: 1.875rem;
    line-height: 1.3;
    margin-bottom: 2rem;
    padding: 0 0.5rem;
    word-wrap: break-word;
  }

  #home .text-xl {
    font-size: 1rem;
    line-height: 1.5;
    margin-bottom: 2rem;
    padding: 0 0.5rem;
    word-wrap: break-word;
  }

  #home .text-lg {
    font-size: 0.9rem;
    line-height: 1.7;
    margin-bottom: 3rem;
    padding: 0 0.5rem;
    word-wrap: break-word;
  }

  /* Button layout - MORE SPACING */
  #home .flex.space-x-4 {
    flex-direction: column;
    gap: 1.5rem;
    align-items: center;
    width: 100%;
    margin-top: 1rem;
  }

  #home .flex.space-x-4 > * {
    width: 100%;
    max-width: 280px;
    padding: 0.875rem 1.5rem;
    font-size: 1rem;
  }

  /* Social icons - MORE SPACING */
  #home .flex.space-x-6 {
    justify-content: center;
    gap: 2rem;
    margin-top: 3rem;
  }

  /* Floating badge - BETTER POSITIONING */
  #home .absolute.bottom-4.right-4 {
    position: fixed;
    bottom: 3rem;
    right: 1rem;
    z-index: 100;
    font-size: 0.875rem;
    padding: 0.75rem 1.25rem;
  }

  /* Scroll up button */
  .fixed.bottom-4.right-4 {
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
    z-index: 100;
  }

  /* Hide problematic elements */
  #home .absolute.w-1.h-1,
  #home .absolute.top-32,
  #home .absolute.top-48 {
    display: none;
  }

  /* Background */
  #home .absolute.inset-0 {
    z-index: -1;
  }

  /* Language dropdown */
  .absolute {
    max-width: calc(100vw - 2rem);
    min-width: 200px;
    z-index: 1000;
  }

  /* Mobile menu */
  .md\\:hidden {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 50;
  }

  /* Grid layouts - CLEAN AND SYMMETRICAL */
  .grid {
    gap: 1rem;
    width: 100%;
  }

  /* Technology grid - 2 columns on mobile */
  .grid.grid-cols-2.sm\\:grid-cols-3.md\\:grid-cols-4 {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }

  /* Other grids - single column */
  .grid-cols-1.md\\:grid-cols-2,
  .grid-cols-1.md\\:grid-cols-3 {
    grid-template-columns: repeat(1, 1fr);
  }

  /* Technology cards */
  .grid.grid-cols-2.sm\\:grid-cols-3.md\\:grid-cols-4 > * {
    padding: 1rem;
    text-align: center;
  }

  .grid.grid-cols-2.sm\\:grid-cols-3.md\\:grid-cols-4 .text-3xl {
    font-size: 2rem;
    margin-bottom: 0.5rem;
  }

  .grid.grid-cols-2.sm\\:grid-cols-3.md\\:grid-cols-4 .font-semibold {
    font-size: 0.875rem;
    margin-bottom: 0.25rem;
  }

  .grid.grid-cols-2.sm\\:grid-cols-3.md\\:grid-cols-4 .text-sm {
    font-size: 0.75rem;
  }

  /* Flex layouts */
  .flex-wrap {
    flex-wrap: wrap;
  }

  .gap-4 {
    gap: 1rem;
  }

  .gap-6 {
    gap: 1.5rem;
  }

  /* Spacing */
  .space-x-4 {
    gap: 1rem;
  }

  .space-x-4 > * {
    margin-left: 0;
  }

  .space-y-2 > * + * {
    margin-top: 0.5rem;
  }

  .space-y-4 > * + * {
    margin-top: 1rem;
  }

  /* Touch targets */
  button, a {
    min-height: 44px;
    min-width: 44px;
  }

  /* Prevent zoom */
  input, textarea, select {
    font-size: 16px;
  }

  /* Forms */
  form {
    width: 100%;
    max-width: 100%;
  }

  form input,
  form textarea,
  form select {
    width: 100%;
    max-width: 100%;
    margin-bottom: 1rem;
    padding: 0.75rem;
    font-size: 16px;
  }

  /* Text elements */
  p, span, div {
    word-wrap: break-word;
    overflow-wrap: break-word;
  }

  /* Section spacing */
  .py-16.md\\:py-20 {
    padding-top: 3rem;
    padding-bottom: 3rem;
  }

  /* Card spacing */
  .p-8.rounded-2xl {
    padding: 2rem;
  }

  .p-4.rounded-xl {
    padding: 1rem;
  }

  /* Progress bars */
  .bg-gradient-to-r.from-blue-500.via-purple-500.to-pink-500 {
    height: 0.75rem;
    border-radius: 0.375rem;
    overflow: hidden;
  }

  /* Skill text */
  .text-sm.text-gray-400,
  .text-sm.text-gray-600 {
    font-size: 0.875rem;
    line-height: 1.25;
  }

  /* Skill names */
  .font-semibold.text-white,
  .font-semibold.text-gray-900 {
    font-size: 1rem;
    line-height: 1.25;
    word-wrap: break-word;
  }

  /* General text sizing */
  .text-2xl {
    font-size: 1.5rem;
    line-height: 1.25;
  }

  .text-xl {
    font-size: 1.25rem;
    line-height: 1.25;
  }

  .text-lg {
    font-size: 1.125rem;
    line-height: 1.25;
  }
}

/* Tablet-specific fixes */
@media (min-width: 769px) and (max-width: 1024px) {
  .section-padding {
    @apply py-16;
  }
}

/* Landscape orientation fixes */
@media (orientation: landscape) and (max-height: 500px) {
  .section-padding {
    @apply py-8;
  }
}

/* High DPI displays */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  /* Ensure crisp rendering on high DPI displays */
  img, svg {
    image-rendering: -webkit-optimize-contrast;
    image-rendering: crisp-edges;
  }
}
